.statisticsContainer {
    max-width: 50%;
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 0.5rem;
  }
  
  .title {
    font-family: 'Bruno Ace SC', sans-serif;
    margin: 0;
    padding: 2rem;
    text-transform: uppercase;
    text-align: center;
    color: #808080;
    font-weight: bold;
    font-size: 2.5rem;
    border-bottom: 2px #d3d5e3;
  }
  
  .statList {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  .statList > .statListItem,
  .statList > .statListItemNoTitle {
    display: flex;
    flex-direction: column;
    text-align: center;
    text-shadow: 0 0 3px #000;
  }
  
  .statListItem:first-child {
    border-radius: 0 0 0 0.5rem;
  }
  
  .statListItem:last-child {
    border-radius: 0 0 0.5rem;
  }
  
  .statListItemNoTitle:first-child {
    border-radius: 0.5rem 0 0 0.5rem;
  }
  
  .statListItemNoTitle:last-child {
    border-radius: 0 0.5rem 0.5rem 0;
  }
  
  .label {
    font-family: 'Bruno Ace SC', sans-serif;
    padding-top: 16px;
    font-size: 1rem;
    color: #fff;
  }
  
  .percentage {
    font-family: 'Bruno Ace SC', sans-serif;
    padding: 1.3rem;
    font-size: 20px;
    font-weight: bold;
    color: #fff;
  }
  